@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.product {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.product > * {
  margin-bottom: 16px;
}

.product__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.product__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.product__button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.product__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.price {
  justify-content: space-between;
  height: 18px;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.reviews {
  justify-content: space-between;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.reviews__text {
  font-size: 10px;
  line-height: 16px;
}

.stars {
  gap: 4px;
  height: 16px;
  list-style: none;
  display: flex;
}

.stars__star {
  aspect-ratio: 1;
